import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'

import "./navbar.scss"

export default function Navbar() {


    return (
        <nav className='Navbar'>
            <NavLink className='Navbar-link' activeClassName to='/'> </NavLink>
            <NavLink className='Navbar-link' activeClassName to='/curs'></NavLink>
            <NavLink className='Navbar-link' activeClassName to='/news'></NavLink>
            <NavLink className='Navbar-link' activeClassName to='/nastav'> </NavLink>
            <div className='Navbar-animation start-home'></div>
        </nav>
    )
}
